<template>
    <div class="container">
        <div>
            <b-table :data="coordenadas" :columns="colunas"></b-table>
        </div>   
    </div>   
</template>

<script>
export default {
    name: 'Geolocation',
    data(){
        return {
            coordenadas: [],
            colunas:[
                {
                    field:'latitude',
                    label:'Latitude'
                },
                {
                    field:'longitude',
                    label:'Longitude'
                },
                {
                    field:'timestamp',
                    label:'Timestamp'
                }
            ]
        }        
    },
    mounted: function(){
        this.geolocation();
    },
    methods:{
        geolocation: function(){
            navigator.geolocation.watchPosition((position)=>{
                let posicaoAtual = {
                    latitude: position.coords.latitude,
                    longitude: position.coords.longitude,
                    timestamp: position.timestamp
                }                
                this.coordenadas.push(posicaoAtual)                
            }, function(error){ 
                console.log(error) 
            }, {enableHighAccuracy: true, maximumAge: 30000, timeout: 30000})
        }
    }
}
</script>

